<script>
import DataService from '../../services/DataService';
import AvailableContract from '../Biller/AvailableContract.vue';

export default {
    components: { AvailableContract },
    data() {
        return {
            isLoading: false,
            companyId: null,
            userId: null,
            isAddContract: false,
            ContractBillerListData: [],
            BillerListData: [],
            AssignBillerData: [],
            FormSchema: [{
                layoutType: "triple",
                Data: [{
                    astype: "SelectField",
                    label: this.$t('Biller'),
                    name: "Biller",
                    value: "",
                    placeholder: this.$t('Select Biller'),
                    validationRules: {
                        "required": true
                    },
                    config: {
                        options: [],
                    },
                    visibility: true
                }]
            }]
        }
    },
    created: async function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        if (this.groupId > 0) {
            vm.GetAssignBillerById(groupId);
        }
        vm.GetBillerList();
        vm.GetContractBillerList();
    },
    methods: {
        AddContract(){
            var vm = this;
            vm.isAddContract = true;
        },
        GetAssignBillerById: function (groupId) {
            var vm = this;
            //vm.isLoading = true;
            var url = `groupId=${this.groupId}&groupBy=Biller`
            DataService.GetAssignBillerById(url).then((response) => {
                if (response.data.result.length > 0) {
                    vm.AssignBillerData = response.data.result;
                }
                //vm.isLoading = false;
            });
        },
        GetBillerList: function () {
            var vm = this;
            //vm.isLoading = true;
            var url = `reqFrom=All&moduleName=Billing`
            DataService.GetBillerList(url).then((response) => {
                if(response.data != ''){
                    vm.BillerListData = response.data.result;
                    vm.BillerListData.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[0].config.options.push({
                            name: itm.USERNAME,
                            value: itm.USERID
                        })
                    })
                }
                vm.$refs.AssignBiller.UpdateKeyValue();
                //vm.isLoading = false;
            });
        },
        GetContractBillerList: function () {
            var vm = this;
            //vm.isLoading = true;
            var url = `groupId=&groupBy=Biller`
            DataService.GetContractBillerList(url).then((response) => {
                if (response.data.result.length > 0) {
                    vm.ContractBillerListData = response.data.result;
                }
                //vm.isLoading = false;
            });
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                Biller: formfields.Biller,
                IsFlag: formfields.IsFlag,
                IsInvoice: formfields.IsInvoice,
                IsUnprocess: formfields.IsUnprocess,
                IsBilledUnBilled: formfields.IsBilledUnBilled,
                ClientId: formfields.ClientId,
            };
            var params = JSON.stringify(jsonobj);
            DataService.SaveAssignBiller(params).then((response) => {
                if (response.data.status == 'Success') {
                    var rslt = JSON.parse(response.data.result);
                    if (rslt.MSG == 'ALREADY EXISTS') {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("Biller")]), "warning", true, vm.$t("Alert"));
                    }
                    else {
                        if (vm.CrewId > 0) {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Biller")]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Biller")]), "success", true, vm.$t("Alert"));
                        }
                        vm.Close();
                        vm.$parent.FetchData();
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Biller")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        },
        Close: function () {
            this.$parent.isAddBiller = false;
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            //vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            } else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {
                    var url = `billerId=${val}&billerInfoId=`;
                    DataService.DeleteAssignBiller(url).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('AssignBiller')]), "success", true, vm.$t("Alert"));
                            vm.GetContractBillerList();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('AssignBiller')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            } else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        }
    }
}
</script>