<script>
import DataService from '../../services/DataService';
export default {
    props: {
        ClientId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            DetailsData: [],
            buttons: [{
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Close",
                onClick: this.Close,
            }]
        }
    },
    created: async function () {
        var vm = this;
        await vm.GetAssignBillerDetail();
    },
    methods: {
        GetAssignBillerDetail() {
            var vm = this;
            //vm.isLoading = true;
            var url = `clientId=${vm.ClientId}`;
            DataService.GetAssignBillerDetail(url).then((response) => {
                if (response.data != '') {
                    vm.DetailsData = [];
                    vm.DetailsData = response.data.result;
                }
            });
            //vm.isLoading = false;
        },
        Close: function () {
            this.$parent.isBillerDetail = false;
        }
    }
}
</script>